import React from 'react';
import ReactDOM from 'react-dom';
import Loadable from 'react-loadable';
import Loader from './Loader'

const LoadableOtherComponent = Loadable({
    loader: () => import('./App'),
    loading: () => <Loader />,
});


ReactDOM.render(
    <LoadableOtherComponent />
    ,
    document.getElementById('app')
);

